.card-icon-text{
    display: flex;
    align-items: center;
    gap: 12px;
    .card-img{
        width: 48px;
        height: 48px;
        background-color: $primaryLight;
        padding: 10px;
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    .card-text{
        line-height: normal;
        flex: 1;
        @include font-size(14px);
    }
}
.card-gray{
    padding: 40px;
}
table{
    &.table-2{
        thead{
            th, td{
                vertical-align: middle;
                background-color: $primaryDark;
                color: $white!important;
            }
        }
        th,td{
            border-bottom: 1px solid $textDark;
            &:last-child{
                text-align: right;
            }
        }
        tbody{
            th,td{
                border-color: rgba($textDark, 0.3);
                &:last-child{
                    font-weight: 400;
                }
            }
            tr{
                &:last-child{
                    th,td{
                        border-color: $textDark;
                    }
                }
            }
        }
    }
}
.chart-label-wrap{
    ul{
        list-style: none;
        padding: 0;
        margin: 0;
        li{
            position: relative;
            padding-left: 25px;
            @include font-size(14px);
            line-height: normal;
            &+li{
                margin-top: 20px;
            }
            &::before{
                content: "";
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                width: 14px;
                height: 14px;
                border-radius: 50%;
                background-color: $primary;
            }
            &.label-primary{
                &::before{
                    background-color: $chartLabel1;
                }
            }
            &.label-light{
                &::before{
                    background-color: $chartLabel2;
                }
            }
            &.label-dark{
                &::before{
                    background-color: $chartLabel3;
                }
            }
        }
    }
    @include media-breakpoint-down(md){
        ul{
            display: flex;
            flex-wrap: wrap;
            gap: 20px 0;
            li{
                width: 50%;
                @include font-size(12px);
                margin-top: 0!important;
            }
        }
    }
}
.partner-swiper{
    .swiper-wrapper{
        transition-timing-function: linear!important;
        will-change: tranform;
    }
    .swiper-slide{
        width: auto;
        border-right: 1px solid $accent5;
        img{
            width: 210px;
            height: 100px;
            object-fit: contain;
            filter: grayscale(1);
            padding: 0 20px;
            cursor: pointer;
            transition: .3s;
            user-select: none;
            &:hover{
                filter: grayscale(0);
            }

            @include media-breakpoint-down(md){
                width: 130px;
                height: 55px;
                padding: 0 15px;
            }
        }
    }
}
.scroll-img{
    &::-webkit-scrollbar {
        height: 2px;
    }
    
    /* Track */
    &::-webkit-scrollbar-track {
        background: $accent5;
    }
    
    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: $textDark;
    }
    @include media-breakpoint-down(lg){
        img{
            width: initial!important;
        }
    }
}
.row-scroll{
    @include media-breakpoint-down(md){
        overflow-x: auto;
        flex-wrap: nowrap;
    }
}