.team-card-wrapper{
    .team-card{
        cursor: pointer;
        &::after{
            content: "\e915";
            font-family: "icomoon";
            position: absolute;
            top: 0;
            right: 0;
            width: 39px;
            height: 39px;
            margin-left: auto;
            display: flex;
            align-items: center;
            justify-content: center;
            color: $textDark;
            font-size: 11px;
            z-index: 1;
            background-color: transparent;
            transition: .3s;
            line-height: 0;
        }
        .card-img{
            padding-bottom: 116%;
        }
        .card-img-overlay {
            background: linear-gradient(0deg, rgba($black, 0.7), rgba($black, 0.6), transparent);
            padding: 20px 16px 16px;
            .card-title{
                @include font-size(16px);
            }
            .card-text{
                // @include font-size(13px);
            }
        }
        &:hover{
            &::after{
                background-color: $secondary;
            }
        }
        &:not([data-fancybox]){
            &::after{
                display: none;
            }
        }
        @include media-breakpoint-down(lg){
            &::after{
                background-color: $secondary;
            }
        }
    }
}
.fancy-wrapper{
    color: $textDark;
    .team-img{
        background-color: $accent2;
        @include media-breakpoint-up(md){
            max-width: 235px;
        }
        img{
            width: 100%;
        }
    }
    .small-text{
        @include font-size(11.8px); 
    } 
    .text-list{
        list-style: none;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        gap: 10px 0;
        li{
            line-height: normal;
            @include media-breakpoint-up(md){
                padding: 0 13px;
                &+li{
                    border-left: 1px solid $primary;
                }
                &:first-child{
                    padding-left: 0;
                }
            }
        }
        @include media-breakpoint-down(md){
            gap: 10px!important;
        }
    }
    .team-social-media{
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 10px;
        li{
            a{
                line-height: normal;
                display: inline-block;
                text-decoration: none;
                outline: none!important;
                height: 20px;
                width: 20px;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
        }
    }
    .play-btn{
        outline: none!important;
    }
    .team-content{
        @include media-breakpoint-up(md){
            max-height: 500px;
        }
        &::-webkit-scrollbar-track {
            background: $accent5;
        }
        &::-webkit-scrollbar-thumb {
            background: $textDark;
        }
    }
}
.founder-swiper{
    .swiper-wrapper{
        @include media-breakpoint-up(lg){
            width: initial;
        }
        @include media-breakpoint-down(lg){
            flex-wrap: nowrap;
        }
    }
}