:root {  
   scroll-behavior: auto;  
   --header-height: 90px;
   --container-fluid-padding: 120px;
//--container-fluid-width: 1920px;
    --container-fluid-width: 1500px;
   @include media-breakpoint-up(xxl){
    .container-fluid{
        max-width: var(--container-fluid-width);
        padding-left: var(--container-fluid-padding);
        padding-right: var(--container-fluid-padding);
    }
   }
   @include media-breakpoint-up(xl){
    .container{
        max-width: 1030px;
    }
   }
}
.resize-animation-stopper * {
    animation: none !important;
    transition: none !important;
}  

::selection {
    background: $primary;
    color: #ffffff !important;
}

::-webkit-input-placeholder {
    opacity: 1 !important;
    color: $placeholderColor !important;
}

:-moz-placeholder {
    opacity: 1 !important;
    color: $placeholderColor !important;
}

::-moz-placeholder {
    opacity: 1 !important;
    color: $placeholderColor !important;
}

:-ms-input-placeholder {
    opacity: 1 !important;
    color: $placeholderColor !important;
}

a {   
    color: $linkColor;
}
b,
strong {
    font-weight: 700;
}
p{
    &:last-child{
        margin-bottom: 0;
    }
}
h1 {
    @include font-size($h1size)
}

h2 {
    @include font-size($h2size)
}

h3 {
    @include font-size($h3size)
}

h4 {
    @include font-size($h4size)
}

h1,
h2,
h3,
h4 {
    font-family: $headingFont;   
    font-weight: 700;
}
.h4{
    @include font-size($h4size!important);
}
.h2{
    @include font-size($h2size!important); 
}
.fs-20{
    @include font-size(20px!important);
}
.ff-heading{
    font-family: $headingFont;
}
.btn{
    padding: 14px 20px;
    border: 0;
    border-radius: 0;   
    display: inline-flex;
    align-items: center;
    justify-content: center; 
    font-weight: 700;
    @include font-size(14px);
    min-width: 130px;
    &:focus-visible{
        outline: auto !important;
    } 
    &.btn-secondary{
        color: $black;
    }  
    &.btn-outline-black{
        border: 1px solid $textDark;
        &:hover{
            color: $white;
            background-color: $textDark;
        }
    } 
    &.btn-outline-white{
        border: 1px solid $white;
        color: $white;
        &:hover{
            background-color: $white;
            color: $textDark;
        }
    } 
}
.btn-wrap{
    display: inline-flex;
    flex-wrap: wrap;
    gap: 16px;

}
.btn-download{
    .btn-arrow-primary{
        &:after{
            content: "\e905";
        }
    }
}
.arrow-link{
    color: $secondary !important;
    text-decoration: none;
    display: inline-flex;
    align-items: center;
    font-weight: 700;
    transition: all 0.3s;
    @include font-size(14px);
    gap: 8px;
    &:after{
        font-family: 'icomoon' !important;
        line-height: 1;
        font-weight: normal;
        transition: transform .3s;
        will-change: transform;
    }
    &.right{
        &::after{
            content: "\e908";
        }
    }
    &:hover{
        &.right{
            &::after{
                transform: translateX(5px);
            }
        }
    }
}

.bg-secondary{
    background-color: $secondary !important;
}
.bg-accent-3{
    background-color: $accent3;
}
.bg-dark{
    background-color: $black !important;
    color: $white;
}
.bg-light{
    background-color: $lightGray !important;
}
.bg-primary-dark{
    background-color: $primaryDark2;
}
.bg-gray-50{
    background-color: $gray-50;
}
.bg-accent-6{
    background-color: $accent6;
}
.text-lightGray{
    color: $lightGray;
}
.bg-light-1{
    background-color: $gray-50;
}
.bg-off-white{
    background-color: #{var(--ms-off-white)}!important;
}
.text-darkgray{
    color: $darkGray;
}
.text-gray-500{
    color: $gray-500;
}
.text-primary-light{
    color: $primaryLight;
}
.text-primary-dark{
    color: $primaryDark;
}
.text-same-color{
    &::first-letter{
        color: inherit!important;
    }
}
.link-href{
    display: inline-flex;
    align-items: center;
    gap: 8px;
    line-height: normal;
    a{
        color: $textDark;
        &:hover{
            color: $primary;
        }
    }
}
.cursor-none{
    cursor: none;
}
.search-toggle{
    text-decoration: none;
    color: $white;
    font-size: 14px;
}
.fs-12{
    @include font-size(12px);
}
.fs-14{
    @include font-size(14px);
}
body {
    font-family: $bodyFontFamily;
    line-height: $bodyLineHeight;
    overflow-x: hidden;
    color: $bodyTextColor;
    position: relative;
    font-size: $bodyFontSize;
    font-weight: 500;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    &:has(.section-inner-hero){
        main{
            padding-top: var(--header-height);
        }
        .header-main{
            background-color: $primaryDark!important;
            &.active{
                a:not(.btn){
                    color: $white;
                }
                .logo{
                    .color-logo{
                        display: none;
                    }
                    .white-logo{
                        display: block;
                    }
                }
                .menu-toggle .line{
                    background-color: $white;
                }
            }
        }
    }
    &.landing-page{
        .header-main{
            .hide-nav{
                display: none;
            }
        }
    }
}

.wrapper{
    overflow: hidden;    
}
.header-main {
    position: fixed;
    left: 0;
    right: var(--fancybox-scrollbar-compensate, 0px);
    top: 0;
    z-index: 100;    
    //margin-right: var(--fancybox-scrollbar-compensate, 0px);
    padding: 20px 0;
    transition: background-color .3s ease-in-out, padding .3s ease-in-out;
    backface-visibility: hidden;
    &.active{
        background-color: $white;
        a{
            color: $bodyTextColor;
        }
        .logo{
            .color-logo{
                display: block;
            }
            .white-logo{
                display: none;
            }
            //filter: invert(1);
        }
        .menu-toggle .line{
            background-color: $black;
        }
    }
}

.menu-toggle{
    display: block;
    padding: 0;
    outline: none!important;
    .line{
        width: 20px;
        height: 2px;
        margin: 3px auto;
        display: block;
        background-color: $white;
        transition: all 0.3s ease-in-out;
        pointer-events: none;
        border-radius: 30px;
    }
    &.is-active{
        .line:nth-child(2){
            opacity: 0;
        }
        .line:nth-child(1){
            transform: translateY(6px) rotate(45deg);
        }
        .line:nth-child(3){
            transform: translateY(-4px) rotate(-45deg);
        }

    }
}

.main-nav {
    >ul {           
        >li {
            position: relative;          
            padding: 0 12px;
            &:last-child{
                padding-right: 0;
            }
            &.has-submenu {
                padding-right: 18px;
                &:before {
                    content: "\e918";
                    font-family: 'icomoon';
                    font-size: 10px;
                    position: absolute;
                    right: 0px;
                    top: 10px;
                    color: $white;
                }
                &:hover {
                    &:before {
                        transform: rotate(180deg);
                    }
                    .submenu {
                        display: block;
                    }
                }
            }           
                
            .submenu {
                display: none;
                position: absolute;
                top: 100%;
                right: 0;
                background: #fff;
                min-width: 120px;
                box-shadow: 0 4px 8px rgba(0,0,0,0.1);
                z-index: 10;
                padding: 0;
                li {
                    list-style: none;
                    border-bottom: 1px solid #eee;
                    &:last-child {
                        border-bottom: none;
                    }
                    a {
                        display: block;
                        padding: 10px 18px;
                        color: #222 !important;
                        font-weight: 500;
                        text-decoration: none;
                        font-size: 15px;
                        white-space: nowrap;
                        &:hover {
                            background: #f0f0f0; 
                        }
                    }
                }                
            }
            > a{                    
                color: $white;      
                display: block;
                text-align: center;      
                text-decoration: none !important;
                transition: all 0.3s;
                position: relative;
                &:not(.btn) {
                    padding-top: 3px;
                    padding-bottom: 3px;
                }
                &::before{
                    content: attr(data-text);
                    font-weight: 700;
                    visibility: hidden;
                    opacity: 0;
                    display: block;
                    height: 1px;
                }
                &.btn{
                    &::before{
                        display: none;
                    }
                }
                &:hover, &.active{
                    &:not(.btn){
                        color: $primary!important;
                        font-weight: 700;
                    }
                }
            }       
        }
    }
}

