.logo { 
    width: 113px;
    display: block;
    transition: width .3s ease-in-out;
    backface-visibility: hidden;
    .color-logo{
        display: none;
    }
    img, svg {
        width: 100%;
        height: auto;       
        display: block;
    }
}

.spinner {
    display: inline-block;
    vertical-align: middle;
    width: 40px;
    height: 40px;
    position: relative;
}

.spinner:after,
.spinner:before {
    content: "";
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $primary;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;
    animation: sk-bounce 2s infinite ease-in-out;
}

.spinner:after {
    animation-delay: -1s;
}

@keyframes sk-bounce {
    0%,
    100% {
        transform: scale(0);
    }
    50% {
        transform: scale(1);
    }
}

.fancybox__backdrop{
    background: rgba($black, 0.50) !important;
}
.swiper-controls{
    min-width: 204px;
    max-width: 204px;
    padding: 5px 15px;
    display: inline-flex;
    background-color: $primary;
    z-index: 1;
   // gap: 18px;
    .swiper-btn-prev, .swiper-btn-next{
        display: flex;
        align-items: center;
        color: $white;
        padding: 10px 10px;
        &::before{
            font-family: "icomoon";
            font-size: 1rem;
        }
        &.swiper-button-disabled{
            opacity: 0.5;
        }
    }
    .swiper-btn-prev{
        &::before{
            content: "\e90d";
        }
    }
    .swiper-btn-next{
        &::before{
            content: "\e90e";
        }
    }
    &:has(.swiper-pagination-lock){
        display: none;
    }
    @include media-breakpoint-down(md){
        min-width: 150px;
        max-width: 150px;
    }
}
.swiper-pagination{
    --swiper-pagination-bullet-size: 8px;
    --swiper-pagination-bullet-horizontal-gap: 4px;
    position: initial;
    transform: none!important;
    margin: auto;
    .swiper-pagination-bullet {
        //left: 0!important;
        transform: scale(1)!important;
        background-color: $white;
        opacity: 0.3;
        &.swiper-pagination-bullet-active{
            opacity: 1;
        }
    }
}
.swiper:not(.swiper-initialized){
    .swiper-controls{
        display: none;
    }
}
.scrollbar{
    &::-webkit-scrollbar {
        width: 6px;
        border-radius: 30px;
    }
    
    /* Track */
    &::-webkit-scrollbar-track {
        background: transparent;
        border-radius: 30px;
    }
    
    /* Handle */
    &::-webkit-scrollbar-thumb {
        background: transparent;
        border-radius: 30px;
        transition: .3s;
    }
}
.scroll-y{
    overflow-y: auto!important;
    padding-right: 20px;
    @extend .scrollbar;

    &:hover{
        &::-webkit-scrollbar-thumb {
            background: $primaryLight;
        }
    }
}
.scroll-x{
    overflow-x: auto!important;
    @extend .scrollbar;
}
.play-btn{
    position: absolute;
    bottom: 0;
    right: 0;
    width: 80px;
    height: 80px;
    background-color: $secondary;
    color: $textDark;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    i{
        transition: .3s;
        will-change: transform;
    }
    &:hover{
        i{
            transform: scale(1.15);
        }
    }
    &.play-btn-sm{
        width: 48px;
        height: 48px;
    }
    @include media-breakpoint-down(md){
        width: 56px;
        height: 56px;
    }
}
.fancybox__backdrop{
    background-color: rgba($textDark, 0.7)!important;
    backdrop-filter: blur(8px);
}
.fancybox__content{
    padding: 50px 40px;
    .f-button {
        &.is-close-btn{
            background-color: transparent;
            color: $textDark;
            top: 10px!important;
            right: 10px!important;
        }
    }
    @include media-breakpoint-down(md){
        padding: 50px 20px;
    }
}
.team-fancybox{
    @include media-breakpoint-down(md){
        >.fancy-wrapper{
            height: calc(100dvh - 130px);
            overflow-x: hidden;
            overflow-y: auto!important;
            padding-right: 10px;
            &::-webkit-scrollbar {
                width: 2px;
                border-radius: 30px;
            }
            
            /* Track */
            &::-webkit-scrollbar-track {
                background: $accent5;
                border-radius: 30px;
            }
            
            /* Handle */
            &::-webkit-scrollbar-thumb {
                background: $primaryDark;
                border-radius: 30px;
            }
        }
    }
}
.cookies-box {
    position: fixed;
    left: 0px;
    bottom: 32px;
    right: 0px;
    z-index: 1000;
}

.cookies-inner {
    background-color: $white;
    padding: 16px 16px 16px 24px;
    box-shadow: 0px 4px 20px 0px #00000026;
    display: flex;    
    flex-wrap: wrap;
    align-items: center;
    .cookie-left {
        width: calc(100% - 280px);
        padding-right: 24px;
    }
    .cookie-right {
        width: 280px;
    }
}

@media(max-width: 767px) {
    .cookies-inner {
        display: block;
        padding: 15px;
        h6 {
            @include font-size(14px);
            margin-bottom: 2px;
        }
        p {
            @include font-size(12px);
        }
        .cookie-left {
            width: 100%;
            padding-right: 0px;
        }
        .cookie-right {
            width: 100%;
            margin-top: 16px;
            justify-content: flex-end;
        }
    }
}