.card-content{
    padding: 16px;
}
.table>:not(caption)>*>*{
    padding: 12px 24px;
    background-color: transparent;
    @include media-breakpoint-down(lg){
        padding: 10px;
        @include font-size(12px);
    }
}
.table{
    thead{
        th, td{
            font-weight: 700;
            background-color: rgba($primary, 0.3);
            border: 0;
        }
    }
    th,td{
        border-color: rgba($white, 0.3);
    }
}
.accordion-wrapper{
    >ul{
        list-style: none;
        padding: 0;
        margin: 0;
        >li{
            border-bottom: 1px solid rgba($accent5, 1);
            >button, >a{
                color: $textDark;
                padding: 24px 50px 24px 24px;
                background-color: transparent;
                border: 0;
                font-weight: 700;
                @include font-size(14px);
                text-decoration: none;
                position: relative;
                display: block;
                width: 100%;
                text-align: left;
                transition: .3s;
                &::before{
                    content: "\e913";
                    font-family: "icomoon";
                    position: absolute;
                    top: 50%;
                    right: 24px;
                    transform: translateY(-50%) rotate(90deg);
                    will-change: transform;
                    transition: .3s;
                }
            }
            .accordion-content{
                display: none;
                background-color: $primaryDark;
                padding: 24px;
                color: $white;
                .table{
                    th,td{
                        color: $white;
                        //white-space: nowrap;
                    }
                }
            }
            &.active{
                >button, >a{
                    background-color: $primary;
                    color: $white;
                    &::before{
                        transform: translateY(-50%) rotate(-90deg);
                    }
                }
            }
        }
    }
}

.card-small-gap {
    ul {
        li {
            margin-top: 5px !important;
            &:first-child {
                margin-top: 0px !important;
            }
        }        
    }
}