@media screen and (max-width: 1800px){
    .landing-wrapper{
        .ratio{
            --bs-aspect-ratio: 90%;
        }
    }
}
@media screen and (max-width: 1570px){
    .landing-wrapper{
        .ratio{
            --bs-aspect-ratio: 100%;
        }
    }
}
@media screen and (max-width: 1250px){
    .landing-wrapper{
        .ratio{
            --bs-aspect-ratio: 115%;
        }
    }
}
@include media-breakpoint-down(xl) { 
    .main-nav{
        position: fixed;
        left: 0;
        top: 0;
        height: 100%;
        width: 250px;
        background-color: $white;
        padding: 40px 20px;
        overflow: 0;
        visibility: hidden;
        transform: translateX(-250px);
        transition: .3s;
        z-index: 11111;
        &::before{
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            background-image: url(../images/card-layer.svg);
            background-repeat: no-repeat;
            background-size: contain;
            pointer-events: none;
            opacity: 0.1;
            background-position-y: bottom;
        }
        ul{
            display: block;
            li{
                padding: 0 0 10px;
                a{
                    color: $bodyTextColor!important;
                    text-align: left;
                    &.btn{
                        padding: 10px 20px;
                        width: fit-content;
                    }
                }
                &.has-submenu {
                    &:before {
                        color: $bodyTextColor!important;
                    }
                    &:hover {
                        &:before {
                            transform: rotate(0deg);
                        }
                        .submenu {
                            display: none;
                        }
                    }
                    &.open {
                        &:before {
                            transform: rotate(180deg) !important;
                        }
                        .submenu {
                            display: block !important;
                        }
                    }
                    
                }
                .submenu {
                    display: none;
                    position: static;
                    margin-top: 4px;
                    box-shadow: 0px 1px 4px rgba(0, 0, 0, .1);
                    li {
                        padding-bottom: 0px;                        
                    }
                }
            }
        }
    }

    body{
        &.active{
            overflow: hidden;
            .main-nav{
                opacity: 1;
                visibility: visible;
                transform: none;
            }
        }
    }
    .section-cursor{
        display: none!important;
    }
    .section{
        &.section-leaders {
            .section-bg{
                padding-bottom: 40%;
            }
        }
    }
}
@include media-breakpoint-down(lg) { 
    .section{
        &.section-hero{
            .hero-swiper{
                .hero-img{
                    padding-bottom: 60%;
                }
            }
        }
        &.section-leaders {
            .section-bg{
                padding-bottom: 50%;
            }
        }
    }
    .form-card {
        .ratio-4x3{
            height: 523px;
        }
    }
    .team-card-wrapper{
        .swiper-slide{
            &.fade{
                opacity: 1!important;
            }
        }
    }
}
@include media-breakpoint-down(md) { 
    :root{
        --header-height: 69px;
    }
    body, .btn, .card-text, .tag-icon-wrap, .arrow-link, .accordion-content{
        @include font-size(12px!important);
    }
    .logo{
        width: 66px;
    }
    .header-main{
        &.active{
            padding: 10px 0;
        }
    }
    .section{
        padding-top: 48px;
        padding-bottom: 48px;
        &.section-hero{
            --hero-nagative-size: 150px;
            .hero-card{
                .tag{
                    @include font-size(12px);
                    gap: 5px;
                    li+li{
                        padding-left: 10px;
                        &::before{
                            width: 4px;
                            height: 4px;
                        }
                    }
                }
            }
            .hero-top{
                padding-top: 100px;
                &::after{
                    max-width: 280px;
                }
                &::before{
                    max-width: 220px;
                }
            }
            .hero-bottom{
                padding-top: 24px;
            }
            .hero-swiper{
                .hero-img{
                    padding-bottom: 70%;
                }
            }
        }
        &.section-leaders {
            --leaders-nagative-size: 100px;
        }
    }
    .card-img-text{
        .card-icon{
            width: 40px;
            height: 40px;
            font-size: 125%;
        }
        .card-text{
            @include font-size(14px);
        }
    }
    .swiper-controls{
        padding: 0 10px;
        .swiper-btn-next, .swiper-btn-prev{
            padding: 10px 10px;
            &::before{
                font-size: 14px;
            }
        }
    }
    .content{
        &.big-content{
            @include font-size(20px);
        }
    }
    .btn{
        min-width: auto;
    }
    .tabs-wrapper{
        ul{
            li{
                button, a{
                    @include font-size(12px);
                }
            }
        }
    }
    .swiper-card-wrap{
        padding: 20px 20px 60px !important;
        .card-title{
            margin-bottom: 8px;
            @include font-size(20px);
        }
        .card-text{
            font-family: $headingFont;
        }
    }
    .card-body, .card-img-overlay{
        .card-title{
            @include font-size(20px);
        }
        .card-text{
            @include font-size(12px);
        }
    }
    .card{
        &.programmer-card{
            .card-img{
                padding-bottom: 100%;
            }
            .card-body{
                padding: 20px 16px;
                .card-title{
                    line-height: normal;
                    @include font-size(17px);
                }
            }
        }
        &.card-classroom{
            .card-img{
                padding-bottom: 50%;
            }
            .card-tag{
                top: 20px;
                left: 20px;
                @include font-size(11px);
            }
            .card-text{
                font-family: $headingFont;
            }
            .card-body-wrap{
                padding: 20px 20px 50px;
                .tag-icon-wrap{
                    margin-top: 20px;
                    margin-bottom: 20px;
                }
            }
            .card-body{
                .card-title{
                    @include font-size(18px);
                }
            }
        }
        &.card-2{
            .card-body{
                padding: 20px 20px 40px;
                .card-title{
                    @include font-size(18px);
                }
                .card-text{
                    font-weight: 400;
                }
            }
        }
        &.card-big{
            padding: 24px;
        }
        &.card-3{
            padding: 20px;
            >.row{
                --bs-gutter-x: 15px;
            }
            .card-body{
                .card-text{
                    font-weight: 500;
                }
                .card-title{
                    margin-bottom: 11px;
                }
            }
        }
        &.card-4{
            .card-img{
                width: 90px;
            }
        }
        &.card-5{
            .card-body{
                .card-title{
                    @include font-size(14px);
                    padding: .8rem 1.5rem .8rem .8rem;
                }
            }
        }
    } 
    .footer-main{
        .footer-bottom{
            font-family: $headingFont;
            @include font-size(10px);
            font-weight: 400;
            padding-bottom: 30px;
        }
        .footer-links{
            ul{
                li{
                    display: flex;
                    flex-direction: column-reverse;
                    i{
                        color: $white;
                    }
                }
            }
        }
    }
    .form-card {
        .ratio-4x3{
            height: 543px;
        }
    }
    .section-static{
        .content{
            @include font-size(12px);
            h5, ol li::marker{
                @include font-size(12px);
            }
        }
    }
    .section-heading{
        h4{
            @include font-size(18px);
        }
    }
    .card-gray{
        padding: 24px;
    }
    .faqs-wrapper{
        ul{
            li{
                >button, >a{
                    @include font-size(16px);
                    line-height: normal;
                    font-family: $bodyFontFamily;
                }
                .faq-content{
                    @include font-size(12px);
                }
            }
        }
    }
    [card-text-hide]{
        display: none;
    }
    .fancy-wrapper{
        .team-content {
            .content{
                @include font-size(14px);
            }
        }
    }
    .mob-row-scroll{
        .col-12{
            &:first-child{
                margin-right:-30px;
                padding-right: 30px;
            }
        }
    }
    .main-nav {
        ul {
            li {
                .submenu {
                    li {
                        a {
                            font-size: 12px;
                        }
                    }
                }
            }
        }
    }
}
