
.anim-hidden,
.animFade,
.fade{   
    opacity: 0;
}
.stagger{
    opacity: 0;
    transform: translateY(50px);
}
.assets-layer{
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url(../images/card-layer.svg);
    background-repeat: no-repeat;
    background-size: contain;
    pointer-events: none;
    opacity: 0.1;
}
.landing-wrapper{
    .ratio {
        img{
            object-fit: cover;
        }
    }
}
.breadcrumb-nav{
    padding-top: 24px;
}
.breadcrumb{
    @include font-size(12px);
    li{
        a{
            color: $textDark;
            text-decoration: none;
            transition: .3s;
            &:hover{
                color: $primary;
            }
        }
        &.active{
            color: $primary;
        }
    }
}
.section{
    padding-top: 100px;
    padding-bottom: 100px;
    &.section-hero{
        --hero-nagative-size: 220px;
        .hero-top{
            background-color: $primaryDark;
            padding-top: 135px;
            padding-bottom: var(--hero-nagative-size);
            position: relative;
            overflow: hidden;
            &::before{
                @extend .assets-layer;
                max-width: 400px;
                background-position-y: bottom;
                transform: rotate(9deg);
                bottom: -25px;
                left: 25px;
            }
            &::after{
                @extend .assets-layer;
                max-width: 470px;
                background-position: bottom right;
                transform: rotate(-45deg);
                left: auto;
                right: calc(var(--hero-nagative-size) / 3);
                bottom: calc(var(--hero-nagative-size) / 2);
            }
        }
        .hero-card{
            color: $white;
            h1{
                @include font-size(100px);
                line-height: 1.1;
                margin-bottom: 14px;
            }
            .tag{
                @include font-size(20px);
            }
        }
        .hero-bottom{
            margin-top: calc(-1 * var(--hero-nagative-size));
            padding-top: 40px;
        }
        .hero-swiper{
            .hero-img{
                padding-bottom: 40%;
                position: relative;
                overflow: hidden;
                img{
                    position: absolute;
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
            }
        }
    }
    &.section-about{
        .content{
            font-family: $headingFont;
        }
    }
    &.section-leaders{
        --leaders-nagative-size: 220px;
        .section-bg{
            padding-bottom: 24%;
            overflow: hidden;
            position: relative;
            img{
                position: absolute;
                height: 100%;
                object-fit: cover;
                object-position: center;
            }
        }
        .content-swiper-wrapper{
            margin-top: calc(-1 * var(--leaders-nagative-size));
        }
        .swiper-card-wrap{
            padding-bottom: calc((var(--leaders-nagative-size) / 2) - 30px);
        }
    }
    &.section-thankyou{
        background-color: $primaryDark;
    }
    &.section-inner-hero{
        padding-top: 30px!important;
        @include media-breakpoint-up(md){
            padding-top: 80px;
        }
        .inner-hero{
            padding-bottom: 52%;
            overflow: hidden;
            position: relative;
            img{
                position: absolute;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}
.section-heading{
    .big{
        @include font-size(72px);
        margin-bottom: 16px;
        line-height: 1.1;
    }
    .text{
        font-weight: 700;
        a{
            color: $textDark;
            transition: .3s;
            &:hover{
                color: $primary;
            }
        }
    }
}
.tag{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    font-weight: 700;
    li{
        position: relative;
        &+li{
            padding-left: 20px;
            &::before{
                content: "";
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                width: 7px;
                height: 7px;
                background-color: $secondary;
                border-radius: 50%; 
            }
        }
    }
}
.tag-icon-wrap{
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    @include font-size(14px);
    li{
        display: flex;
        align-items: center;
        gap: 8px;
        i{
            font-size: 120%;
            line-height: 0;
        }
    }
}
.brand-logo-icon{
    max-width: 54px;
}
.content{
    &.big-content{
        @include font-size(36px);
    }
    h5{
        @include font-size(20px);
        font-weight: 700;
    }
    ol{
        padding-left: 1.2rem;
        margin: 0;
        li{
            &::marker{
                font-weight: 700;
                font-family: $headingFont;
                @include font-size(20px);
            }
            &+li{
                margin-top: 24px;
            }
        }
    }
    ul{
        list-style: disc;
        padding-left: 1rem;
        li{
            line-height: normal;
            &::marker{
                color: $primary;
            }
            &+li{
                margin-top: 8px;
            }
        }
    }
}
.card-img-text{
    display: flex;
    align-items: center;
    gap: 12px;
    color: $textDark;
    text-decoration: none;
    .card-icon{
        width: 68px;
        height: 68px;
        background-color: $primaryDark;
        display: flex;
        align-items: center;
        justify-content: center;
        color: $primaryLight;
        font-size: 220%;
    }
    .card-text{
        @include font-size(24px);
        line-height: normal;
    }
}
.tabs-wrapper{
    ul{
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        gap: 16px;
        li{
            button, a{
                display: inline-block;
                border: 0;
                background-color: $lightGray;
                padding: 10px 24px;
                @include font-size(14px);
                font-weight: 500;
                transition: .3s;
                white-space: nowrap;
                color: $bodyTextColor;
                &::before{
                    content: attr(data-text);
                    visibility: hidden;
                    opacity: 0;
                    font-weight: 700;
                    height: 0;
                    display: block;
                }
            }
            &.active, &:hover{
                button, a{
                    background-color: $textDark;
                    color: $white;
                    font-weight: 700;
                }
            }
        }
    }
    &.tabs-left{
        width: 204px;
        ul{
            display: block;
            li{
                &+li{
                    border-top: 1px solid $accent5;
                }
                button, a{
                    background-color: $white;
                    width: 100%;
                    text-align: left;
                    padding: 22px 35px 22px 16px;
                    position: relative;
                    white-space: normal;
                    &::after{
                        content: "\e908";
                        font-family: "icomoon";
                        position: absolute;
                        right: 16px;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }
                &.active{
                    button, a{
                        padding-right: 16px;
                        &::after{
                            display: none;
                        }
                    }
                }
                &.active, &:hover{
                    button, a{
                        background-color: $primary;
                    }
                }
            }
        }
    }
}
.tabs-links{
    >ul{
        overflow-x: auto;
        margin-left: -5000px;
        margin-right: -5000px;
        padding-left: 5000px;
        padding-right: 5000px;
        &::-webkit-scrollbar{
            display: none;
        }
    }
    
}
.tabs-content-wrapper{
    .tab-content-item{
        &:not(.active){
            display: none;
        }
        .team-swiper{
            .swiper-controls{
                &:has(.swiper-pagination-lock){
                    display: none;
                }
            }
        }
    }
    &.tabs-faqs-wrapper{
        padding: 40px;
    }
}
.tab-content-style{
    position: relative;
    z-index: 1;
    color: $white;
    height: 100%;
    @include font-size(14px);
    ul{
        padding-left: 1rem;
        margin: 0;
        list-style: disc;
        li{
            &::marker{
                color: $primary;
            }
            &+li{
                margin-top: 16px;
            }
        }
    }
    .nt-cources-div {
        padding-top: 15px;
        h6 {
            padding: 12px 24px;
        }
        ul {
            list-style-type: none;
            padding-left: 0px;
        }
        li {
            padding: 12px 24px;
            margin-top: 0px;
            border-top: 1px solid #FFFFFF4D;
        }
    }
    ol{
        padding-left: 0;
        margin: 0;
        counter-reset: custom;
        list-style-type: none;
        li{
            counter-increment: custom;
            position: relative;
            padding-left: 40px;
            &+li{
                margin-top: 16px;
            }
            &:before {
                content: counter(custom, decimal-leading-zero);
                position: absolute;
                left: 0;
                width: 24px;
                height: 24px;
                margin-right: 10px;
                border-radius: 50%;
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: $primaryLight;
                color: $textDark;
                @include font-size(10px);
                font-weight: 700;
            }
        }
    }
    .table>:not(caption)>*>*{
        color: $white;
    }
}
.tabs-left-content-wrapper{
    padding: 30px;
    background-color: $primaryDark;
    position: relative;
    overflow: hidden;
    &::before{
        @extend .assets-layer;
        left: auto;
        right: 0;
        bottom: -15px;
        background-position: bottom right;
        transform: rotate(-30deg);
        max-width: 360px;
        z-index: 1;
    }

    .tab-content-item{
        @extend .tab-content-style;
    }
}
.card{
    border-radius: 0;
    border: 0;
    .card-body, .card-img-overlay{
        .card-title{
            font-family: $headingFont;
            font-weight: 700;
        }
    }
    &.team-card{
        background-color: $accent2;
        color: $white;
        text-decoration: none;
        &::before{
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            height: 100%;
            width: 100%;
            background-color: rgba($black, .05);
        }
        .card-img{
            padding-bottom: 100%;
            overflow: hidden;
            img{
                position: absolute;
                height: 100%;
                width: 100%;
                //object-fit: contain;
                //object-position: bottom;
                object-fit: cover;
                object-position: top;
            }
        }
        .card-img-overlay{
            border-radius: 0;
            padding: 50px 27px 27px;
            top: auto;
            z-index: 1;
            background: linear-gradient(0deg, rgba($black, .6), rgba($black, 0.4), transparent);
            .card-title{
                @include font-size(20px);
                margin-bottom: 0;
            }
            .card-text{
                @include font-size(13px);
            }
        }
    }
    &.programmer-card{
        height: 100%;
        >.row{
            height: 100%;
            >div{
                //display: flex;
            }
        }
        .card-img{
            border-radius: 0;
            padding-bottom: 95%;
            overflow: hidden;
            position: relative;
            height: 100%;
            img{
                position: absolute;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .card-body{
            height: 100%;
            background-color: $gray-50;
            position: relative;
            padding: 20px;
            @include media-breakpoint-up(lg){
                padding: 57px;
            }
            &::before{
                @extend .assets-layer;
                background-position: bottom center;
            }
            .card-title{
                @include font-size(24px);
            }
        }
    }
    &.card-classroom{
        height: 100%;
        .card-img{
            position: relative;
            border-radius: 0;
            // padding-bottom: 30%;
            padding-bottom: 40%;
            position: relative;
            overflow: hidden;
            img{
                position: absolute;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .card-tag{
            position: absolute;
            top: 40px;
            left: 40px;
            background-color: $white;
            padding: 4px 8px;
            color: $textDark;
            @include font-size(13px);
            font-weight: 400;
            z-index: 1;
        }
        .card-body-wrap{
            flex: 1 1 auto;
            background-color: $primaryDark;
            color: $white;
            padding: 30px 42px 65px;
            display: flex;
            flex-direction: column;
            .tag-icon-wrap{
                margin-top: 35px;                
                &:has(li){
                    margin-bottom: 35px;
                }
            }
            .link-wrap{
                position: relative;
                .arrow-link{
                    position: absolute;
                }
            }
        }
        .card-body, .card-footer{
            padding: 0;
        }
        .card-body{
            .card-title{
                @include font-size(24px);
                line-height: normal;
            }
        }
        .card-footer{
            border-radius: 0;
            border: 0;
            background-color: $primaryDark;
            color: $primaryLight;
        }
    }
    &.card-2{
        .card-img{
            border-radius: 0!important;
            width: 408px;
            padding-bottom: 100%;
            position: relative;
            height: 100%;
            overflow: hidden;
            img{
                position: absolute;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            @include media-breakpoint-down(lg){
                width: 100%;
                padding-bottom: 40%;
            }
        }
        .card-body{
            padding: 40px 40px 60px;
            background-color: $primaryDark;
            height: 100%;
            color: $white;
            position: relative;
            overflow: hidden;
            &::before{
                @extend .assets-layer;
                max-width: 360px;
                left: auto;
                right: 0;  
                bottom: -15px;          
                background-position: bottom right;
                transform: rotate(-30deg);
            }

            .card-title{
                @include font-size(24px);
                margin-bottom: 20px;
            }
        }
        .card-text{
            ul{
                padding-left: 1rem;
                li{
                    &+li{
                        margin-top: 10px;
                    }
                }
            }
        }
    }
    &.card-3{
        background-color: $primaryDark;
        color: $white;
        padding: 40px;
        height: 100%;
        .card-icon{
            max-width: 50px;
            height: 50px;
            img{
                height: 100%;
                width: 100%;
                object-fit: contain;
            }
            @include media-breakpoint-down(md){
                max-width: 30px;
                height: 30px;
            }
        }
        .card-body{
            padding: 0;
        }
        .card-title{
            @include font-size(36px);
            font-weight: 700;
            line-height: 1;
            margin-bottom: 16px;
        }
        .card-text{
            font-weight: 700;
        }
        .card-info-wrap{
            list-style: none;
            padding: 0;
            margin: 0;
            li{
                display: flex;
                gap: 10px;
                align-items: baseline;
                &+li{
                    margin-top: 24px;
                }
                .card-info-title{
                    font-family: $headingFont;
                    font-weight: 700;
                    @include font-size(20px);
                    line-height: 1;
                    margin-bottom: 6px;
                }
                i{
                    color: $primary;
                    font-size: 16px;
                }
                a{
                    color: $white;
                    transition: .3s;
                    &:hover{
                        color: $primary;
                    }
                }
            }
        }
    }
    &.card-big{
        padding: 36px;
        background-color: $accent6;
        .card{
            color: $textDark;
        }
        .card-title{
            &::first-letter{
                color: $primary;
            }
        }
    }
    &.card-4{
        background-color: $accent6;
        padding: 32px 32px;
        height: 100%;
        &:has(.play-btn){
            padding-bottom: 120px;
        }
        .card-footer, .card-body{
            padding: 0;
            background-color: transparent;
            border: 0;
        }
        .card-img{
            width: 131px;
            padding-bottom: 100%;
            overflow: hidden;
            position: relative;
            border-radius: 0;
            border: 0;
            img{
                position: absolute;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .card-title{
            @include font-size(32px);
            font-weight: 700;
            line-height: 1.1;
        }
        .card-text{
            font-weight: 400;
        }
        .play-btn{
            position: absolute;
            bottom: 0;
            right: 0;
        }
    }
    &.card-list{
        height: 100%;
        .card-body{
            padding: 24px;
        }
        .card-title{
            @include font-size(24px);
            font-weight: 700;
            line-height: normal;
            margin-bottom: 3px;
        }
        .card-text{
            color: $primaryLight;
            @include font-size(12px);
        }
        ul{
            list-style: none;
            padding: 0;
            margin: 16px 0 0 0;
            li{
                @include font-size(14px);
                padding: 12px 0;
                &:first-child{
                    padding-top: 0;
                }
                &+li{
                    border-top: 1px solid rgba($textDark, 0.2);
                   
                }
            }
        }
    }
    &.card-5{
        background-color: $accent6;
        width: 100%;
        .row{
            height: 100%;
        }
        .card-img{
            padding-bottom: 60%;
            position: relative;
            overflow: hidden;
            border: 0;
            border-radius: 0;
            img{
                position: absolute;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .card-body{
            padding: 0;
            .card-title{
                padding: 1rem 2rem 1rem 1rem;
                margin-bottom: 0;
            }
        }
        .play-btn{
            height: 100%;
            position: inherit!important;
        }
    }
}
.card-overly-effect{
    position: relative;
    overflow: hidden;
    &::before{
        @extend .assets-layer;
        left: auto;
        right: 0;
        bottom: -15px;
        background-position: bottom right;
        transform: rotate(-30deg);
        max-width: 360px;
        z-index: 1;
    }
}
.content-ul{
    ul{
        padding-left: 1rem;
        margin-bottom: 0;
        li{
            margin-bottom: 16px;
            &::marker{
                color: $primary;
            }
        }
    }
}
.swiper-card-wrap{
    color: $white;
    background-color: $primaryDark;
    position: relative;
    overflow: hidden;
    padding: 55px 60px 70px;
    z-index: 1;
    &::before{
        @extend .assets-layer;
        left: auto;
        right: 0;
        background-position: bottom right;
        transform: rotate(-30deg);
        max-width: 320px;
        z-index: -1;
    }
    .card-title{
        @include font-size(36px);
        margin-bottom: 20px;
    }
    .card-text{
        @include font-size(14px);
        padding-right: 10px;
    }
}
.card-counter{
    .card-number, .card-title{
        font-family: $headingFont;
    }
    .card-number{
        @include font-size(56px);
        font-weight: 400;
        line-height: 1;
    }
    .card-title{
        @include font-size(24px);
        font-weight: 700;
        margin-bottom: 5px;
    }
}
.swiper{
    &.programmer-swiper, &.classroom-swiper, &.visionaries-swiper{
        .swiper-slide{
            height: auto;
        }
    }
    .swiper-wrapper{
        height: initial;
    }
    &.leaders-swiper{
        .swiper-slide:not(.swiper-slide-active){
            visibility: hidden;
        }
    }
    &.classroom-swiper, &.programmer-swiper, &.visionaries-swiper{
        .swiper-slide{
            display: flex;
        }
        .card-classroom, .programmer-card, .card-4, .card-5{
            width: 100%;
        }
    }
}
.section-cursor{
    position: fixed;
    top: 0;
    left: 0;
    width: 150px;
    height: 150px;
    border: 1px solid $primary;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include font-size(14px);
    color: $primary;
    gap: 15px;
    z-index: 1000;
    pointer-events: none;
    visibility: hidden;
    opacity: 0;
    .text{
        font-weight: 700;
    }
    .arrow-prev, .arrow-next{
        display: flex;
        align-items: center;
        padding: 10px 10px;
        &::before{
            font-family: "icomoon";
            font-size: 1rem;
        }
    }
    .arrow-prev{
        &::before{
            content: "\e90f";
        }
    }
    .arrow-next{
        &::before{
            content: "\e908";
        }
    }
}
.footer-main{
    background-color: $textDark;
    color: $white;
    padding-top: 56px;  
    font-family: $headingFont;
    .footer-bottom{
        margin-top: 44px;
        padding: 15px 0;
        border-top: 1px solid rgba($white, 0.2);
        @include font-size(12px);
        font-weight: 400;
        .copyrights, span{
            opacity: 0.7;
        }
        .design-by{
            a{
                text-decoration: none;
                color: $white;
                &:hover{
                    color: $primary;
                }
            }
        }
    }
    .footer-logo{
        max-width: 147px;
        img{
            width: 100%;
        }
    }
    .enquire-btn {
        padding-top: 20px;
    }
    .copyright-links {
        list-style-type: none;
        padding: 0px;
        display: flex;
        flex-wrap: wrap;
        margin: 0 -18px;
        li {
            color: rgba($white, 0.7);
            padding: 0 18px;
            a {
                text-decoration: none;
                color: rgba($white, 0.7);
                &:hover {
                    color: $primary;
                }
            }
        }
    }
    .footer-links{
        .footer-title{
            @include font-size(24px);
            font-weight: 700;
            margin-bottom: 5px;
            .title-link {
                color: $white;
                text-decoration: none;
            }
        }
        ul{
            list-style: none;
            padding: 0;
            margin: 0;
            li{
                @include font-size(14px);
                &+li{
                    margin-top: 20px;
                }
                i{
                    color: $primary;
                    margin-right: 8px;
                    vertical-align: middle;
                }
                a{
                    font-weight: 700;
                    color: $white;
                    text-decoration: none;
                    transition: .3s;
                    &:hover{
                        color: $primary;
                    }
                }
                span{
                    font-weight: 300;
                }
            }
            &.nav-social-media{
                display: flex;
                flex-wrap: wrap;
                gap: 14px;
                li{
                    margin-top: 0;
                    a{
                        i{
                            color: inherit;
                            font-size: 135%;
                        }
                    }
                }
            }
        }
    }
}
.faqs-wrapper{
    >ul{
        list-style: none;
        padding: 0;
        margin: 0;
        >li{
            border-bottom: 1px solid rgba($primary, 0.2);
            padding: 16px 0;
            &:first-child{
                padding-top: 0;
            }
            >button, >a{
                color: $textDark;
                padding: 0 40px 0 0;
                background-color: transparent;
                border: 0;
                font-family: $headingFont;
                font-weight: 700;
                @include font-size(20px);
                text-decoration: none;
                position: relative;
                display: block;
                width: 100%;
                text-align: left;
                &::before{
                    content: "\002B";
                    //font-family: "icomoon";
                    position: absolute;
                    right: 0;
                    font-size: 34px;
                    line-height: .9;
                    font-weight: 200;
                    will-change: transform;
                    transition: .3s;
                }
            }
            .faq-content{
                padding-right: 40px;
                margin-top: 7px;
                display: none;
                @include font-size(14px);
                a{
                    color: $textDark;
                    transition: .3s;
                    &:hover{
                        color: $primary;
                    }
                }
                ul {
                    list-style: disc;                                    
                }
            }
            &.active{
                >button, >a{
                    &::before{
                        content: "\2212";
                    }
                }
            }
        }
    }
}
.accordion-wrapper{
    ul{
        li{ 
            .accordion-content{
                @extend .tab-content-style;
            }
        }
    }
}

.sitemap-box {
    padding: 12px 24px;
    background-color: $gray-50; 
    ul {
        list-style-type: none;
        padding: 0;
        margin: 0;        
        li {            
            border-bottom: 1px solid rgba($color: $primary, $alpha: .2);
            padding: 8px 0;
            &:last-child {
                border-bottom: none;
            }
            a {                
                font-weight: 700;
                font-size: 14px;
                line-height: 120%;
                color: $textDark;
                font-family: $headingFont;  
                text-decoration: none;                                                
                &:hover {
                    color: $primary;
                }
            }
        }
    }
}

