.events-and-news {
  padding-top: 36px;
  padding-bottom: 48px;
  .big {
    margin-bottom: 0;
  }
}
.cards-wrapper {
  display: grid;
  gap: 24px;
  grid-template-columns: repeat(2, 1fr);
  padding-top: 24px;
}
.sort {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  label {
    font-family: 'Inter';
    font-weight: 500;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: -2%;
    text-transform: lowercase;
    margin-right: 4px;
    color: $gray-555555;
  }
  .select-form-control {
    border: 0;
    border-bottom: 1px solid $bodyTextColor;
    height: 40px;
    padding: 8px 16px 8px 4px;
    font-family: 'Inter';
    font-weight: 700;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: -2%;
    text-transform: lowercase;
    color: $bodyTextColor;
    &:focus {
      outline: none;
    }
  }
}
@media (max-width: 991px) {
  .events-and-news {
    padding-top: 12px;
    padding-bottom: 24px;
  }
  .cards-wrapper {
    grid-template-columns: 1fr;
  }
}
@media (max-width: 767px) {
  .sort {
    justify-content: flex-start;
    margin-top: 20px;
  }
}